import GitHubIcon from '@mui/icons-material/GitHub';

import LinkedInIcon from '@mui/icons-material/LinkedIn';


// import ReactTyped from "react-typed";
import ReactType from "../TypedComponent";
import React from 'react';
import { about } from '../../portfolio'
import './About.css'
import Type from '../TypedComponent';

/*************   Codeium Command   *************/
  /**
   * A functional component that renders the About section of the website.
   *
   * This section displays a heading with the user's name, a role description
   * that uses the Type component to animate the text, a text description,
   * and a list of links to the user's resume and social media profiles.
   *
   * @returns {ReactElement} The About section.
   */
/******  fc35f2df-ad8f-4329-aeb5-ee09559eccd5  *******/const About = () => {
  const { name, role, description, resume, social } = about

  return (
    <div className='about center'>
      {name && (
        <h1>
          Hi, I am <span className='about__name'>{name}.</span>
        </h1>
      )}

      {role && <h2 className='about__role'>A {<Type />}.</h2>}
      <h2>
        A <ReactType />
        </h2>


      <p className='about__desc'>{description && description}</p>

      <div className='about__contact center'>
        {resume && (
          <a href={resume}>
            <span type='button' className='btn btn--outline'>
              Resume
            </span>
          </a>
        )}

        {social && (
          <>
            {social.github && (
              <a
                href={social.github}
                aria-label='github'
                className='link link--icon'
              >
                <GitHubIcon />
              </a>
            )}

            {social.linkedin && (
              <a
                href={social.linkedin}
                aria-label='linkedin'
                className='link link--icon'
              >
                <LinkedInIcon />
              </a>
            )}
          </>
        )}
      </div>
    </div>
  )
}

export default About
